<template>
  <div id="app">
    <keep-alive :exclude="['detail']">
      <router-view></router-view>
    </keep-alive>
    <browser-caveat @click="isPC = false" v-if="isPC"></browser-caveat>
  </div>
</template>

<script>
import BrowserCaveat from '@/components/common/browserCaveat/BrowserCaveat'
export default {
  data() {
    return {
      isPC: false
    }
  },
  components: {
    BrowserCaveat
  },
  created() {
    const browser = {
      versions: (function () {
        const u = navigator.userAgent
        return {
          // 是否为移动终端
          mobile: !!u.match(/AppleWebKit.*Mobile.*/)
        }
      })(),
      language: (navigator.browserLanguage || navigator.language).toLowerCase()
    }
    if (browser.versions.mobile) {
      this.isPC = false
    } else {
      // 否则就是PC浏览器打开
      this.isPC = true
    }
  }
}
</script>

<style lang="less">
@import './assets/css/base.css';
</style>
